<script setup lang="ts">
import { ref } from 'vue'
// import { ElMessage } from 'element-plus'

const visible = ref(false)
const loading = ref(false)

// 余额变动记录详情数据
const balanceLogDetail = ref({
  id: 0,
  supplier_id: 0,
  operation_type: 1,
  amount: 0,
  balance_before: 0,
  balance_after: 0,
  operator_type: 1,
  operator_id: null,
  operator_name: '',
  order_id: null,
  order_no: '',
  remark: '',
  telegram_message: '',
  ip_address: '',
  user_agent: '',
  created_at: '',
  updated_at: '',
  supplier: null as any,
  order: null as any
})

// 操作类型选项
const operationTypeOptions = [
  { label: '预付', value: 1 },
  { label: '下发', value: 2 },
  { label: '订单扣款', value: 3 },
  { label: '退款', value: 4 },
  { label: '系统调整', value: 5 }
]

// 操作人类型选项
const operatorTypeOptions = [
  { label: '管理员', value: 1 },
  { label: '系统', value: 2 },
  { label: '订单', value: 3 }
]

// 获取操作类型文本
const getOperationTypeText = (type: number) => {
  const option = operationTypeOptions.find(item => item.value === type)
  return option ? option.label : '未知'
}

// 获取操作人类型文本
const getOperatorTypeText = (type: number) => {
  const option = operatorTypeOptions.find(item => item.value === type)
  return option ? option.label : '未知'
}

// 格式化金额
const formatAmount = (amount: number) => {
  return (amount / 100).toFixed(2)
}

// 格式化时间
const formatDateTime = (dateTime: string) => {
  return new Date(dateTime).toLocaleString('zh-CN')
}

// 获取金额样式类
const getAmountClass = (amount: number) => {
  return amount > 0 ? 'text-success' : 'text-danger'
}

// 获取操作类型标签类型
const getOperationTypeTagType = (type: number) => {
  const typeMap = {
    1: 'success',   // 预付
    2: 'warning',  // 下发
    3: 'info',     // 订单扣款
    4: 'danger',   // 退款
    5: ''          // 系统调整
  }
  return typeMap[type as keyof typeof typeMap] || ''
}

// 打开详情对话框
const open = (row: any) => {
  balanceLogDetail.value = { ...row }
  visible.value = true
}

// 关闭对话框
const close = () => {
  visible.value = false
}

// 查看关联订单
const viewOrder = (orderNo: string) => {
  if (orderNo) {
    window.open(`/order/home?search=${orderNo}`, '_blank')
  }
}

// 查看供应商详情
const viewSupplier = (supplierId: number) => {
  if (supplierId) {
    window.open(`/supplier/home?search=${supplierId}`, '_blank')
  }
}

// 暴露方法给父组件
defineExpose({
  open,
  close
})

// 定义事件
const emit = defineEmits(['refresh'])
</script>

<template>
  <el-dialog
    v-model="visible"
    title="余额变动记录详情"
    width="800px"
    :close-on-click-modal="false"
    @close="close"
  >
    <div v-loading="loading" class="detail-container">
      <!-- 基本信息 -->
      <div class="detail-section">
        <h3 class="section-title">基本信息</h3>
        <el-row :gutter="20">
          <el-col :span="12">
            <div class="detail-item">
              <label>记录ID：</label>
              <span>{{ balanceLogDetail.id }}</span>
            </div>
          </el-col>
          <el-col :span="12">
            <div class="detail-item">
              <label>供应商：</label>
              <el-link 
                v-if="balanceLogDetail.supplier" 
                type="primary" 
                @click="viewSupplier(balanceLogDetail.supplier_id)"
              >
                {{ balanceLogDetail.supplier.supplier_name }}
              </el-link>
              <span v-else>{{ balanceLogDetail.supplier_id }}</span>
            </div>
          </el-col>
        </el-row>
        
        <el-row :gutter="20">
          <el-col :span="12">
            <div class="detail-item">
              <label>操作类型：</label>
              <el-tag :type="getOperationTypeTagType(balanceLogDetail.operation_type)">
                {{ getOperationTypeText(balanceLogDetail.operation_type) }}
              </el-tag>
            </div>
          </el-col>
          <el-col :span="12">
            <div class="detail-item">
              <label>操作时间：</label>
              <span>{{ formatDateTime(balanceLogDetail.created_at) }}</span>
            </div>
          </el-col>
        </el-row>
      </div>

      <!-- 金额信息 -->
      <div class="detail-section">
        <h3 class="section-title">金额信息</h3>
        <el-row :gutter="20">
          <el-col :span="8">
            <div class="detail-item">
              <label>变动金额：</label>
              <span :class="getAmountClass(balanceLogDetail.amount)">
                {{ balanceLogDetail.amount > 0 ? '+' : '' }}¥{{ formatAmount(balanceLogDetail.amount) }}
              </span>
            </div>
          </el-col>
          <el-col :span="8">
            <div class="detail-item">
              <label>变动前余额：</label>
              <span>¥{{ formatAmount(balanceLogDetail.balance_before) }}</span>
            </div>
          </el-col>
          <el-col :span="8">
            <div class="detail-item">
              <label>变动后余额：</label>
              <span>¥{{ formatAmount(balanceLogDetail.balance_after) }}</span>
            </div>
          </el-col>
        </el-row>
      </div>

      <!-- 操作人信息 -->
      <div class="detail-section">
        <h3 class="section-title">操作人信息</h3>
        <el-row :gutter="20">
          <el-col :span="12">
            <div class="detail-item">
              <label>操作人：</label>
              <span>{{ balanceLogDetail.operator_name || '-' }}</span>
            </div>
          </el-col>
          <el-col :span="12">
            <div class="detail-item">
              <label>操作人类型：</label>
              <el-tag size="small">{{ getOperatorTypeText(balanceLogDetail.operator_type) }}</el-tag>
            </div>
          </el-col>
        </el-row>
        
        <el-row :gutter="20">
          <el-col :span="12">
            <div class="detail-item">
              <label>操作IP：</label>
              <span>{{ balanceLogDetail.ip_address || '-' }}</span>
            </div>
          </el-col>
          <el-col :span="12">
            <div class="detail-item">
              <label>用户代理：</label>
              <span>{{ balanceLogDetail.user_agent || '-' }}</span>
            </div>
          </el-col>
        </el-row>
      </div>

      <!-- 关联信息 -->
      <div class="detail-section" v-if="balanceLogDetail.order_no || balanceLogDetail.order">
        <h3 class="section-title">关联信息</h3>
        <el-row :gutter="20">
          <el-col :span="12" v-if="balanceLogDetail.order_no">
            <div class="detail-item">
              <label>关联订单：</label>
              <el-link type="primary" @click="viewOrder(balanceLogDetail.order_no)">
                {{ balanceLogDetail.order_no }}
              </el-link>
            </div>
          </el-col>
          <el-col :span="12" v-if="balanceLogDetail.order">
            <div class="detail-item">
              <label>订单状态：</label>
              <el-tag :type="balanceLogDetail.order.status === 3 ? 'success' : 'info'">
                {{ balanceLogDetail.order.status === 3 ? '支付成功' : '其他状态' }}
              </el-tag>
            </div>
          </el-col>
        </el-row>
      </div>

      <!-- 备注信息 -->
      <div class="detail-section" v-if="balanceLogDetail.remark">
        <h3 class="section-title">备注信息</h3>
        <div class="detail-item">
          <label>备注：</label>
          <div class="remark-content">{{ balanceLogDetail.remark }}</div>
        </div>
      </div>

      <!-- Telegram消息 -->
      <div class="detail-section" v-if="balanceLogDetail.telegram_message">
        <h3 class="section-title">Telegram消息</h3>
        <div class="detail-item">
          <label>原始消息：</label>
          <div class="telegram-message">
            <pre>{{ balanceLogDetail.telegram_message }}</pre>
          </div>
        </div>
      </div>
    </div>

    <template #footer>
      <div class="dialog-footer">
        <el-button @click="close">关闭</el-button>
        <el-button type="primary" @click="emit('refresh')">刷新数据</el-button>
      </div>
    </template>
  </el-dialog>
</template>

